.social__list {
  font-size: 0;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-right: -6px;
}

.social__item {
  margin-right: 6px;
}

.social__link {
  background-color: $blue;
  display: block;
  width: 46px;
  height: 48px;

  &:hover {
    background-color: rgb(102, 158, 192);
  }

  &:active {
    background-color: #5496bd;

    svg {
      opacity: 0.3;
    }
  }
}

.social__link-twitter,
.social__link-facebook,
.social__link-youtube {
  height: 100%;
  fill: #ffffff;
}
