.main-nav {
  &--opened {
    display: block;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      height: 8px;
      width: 100%;
      box-shadow: inset 0 -3px 5px -2px rgba(0, 0, 0, 0.5);
    }
  }

  &--closed {
    display: none;
  }
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media (min-width: $tablet-width) {
  .main-nav {
    display: block;

    &--opened::after {
      display: none;
    }
  }

  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
}
